const handleFeedSuccess = async function (res, upload) {
  if (res.code === 1001) {
    ElMessage({
      type: 'success',
      message: '上传成功'
    })
    feedForm.value.imageUrl = res.data.filePath
    submitFeed()
  } else {
    ElMessage({
      type: 'warning',
      message: res.msg
    })
  }
};

const uploadError = () => {
  ElMessage({
    type: 'error',
    message: '上传失败'
  })
}

const beforeFeedUpload = function (rawFile) {
  token.value = getLocalStorageItem("token")
  if (rawFile.type !== 'image/jpeg' && rawFile.type !== 'image/png' && rawFile.type !== 'image/gif') {
    ElMessage.error('头像只允许JPG、PNG或GIF格式!');
    return false;
  } else if (rawFile.size / 1024 / 1024 > 10) {
    ElMessage.error('上传的头像文件大小不能 10MB!');
    return false;
  }
  return true;
};

window.onscroll = function () {
  let navbar = document.getElementById('header');
  if (window.pageXOffset > 0) {
    navbar.style.left = -window.pageXOffset + 'px';
  } else {
    navbar.style.left = '0';
  }
};


function uploadPic() {
  return new Promise((resolve, reject) => {
    uploadRef.value.submit();
    resolve()
  })
}

function selectPic() {
  uploadRef.value.$el.querySelector('input').click()
}

function removePic() {
  tempPic.value = ''
  uploadRef.value.clearFiles()
}


const imgFeedChange = (file, fileList) => {
  tempPic.value = URL.createObjectURL(file.raw)
}

const postFeed = () => {
  if (tempPic.value) {
    uploadPic()
  } else {
    submitFeed()
  }
}

const submitFeed = () => {
  requestService.post("/feed", feedForm.value).then((res) => {
    if (res.data.code === 3001) {
      openNotificationWithIcon('success', res.data.msg)
      feedForm.value.content = ""
      feedForm.value.imageUrl = ""
      removePic()
      openPost.value = false
      router.push('/recommended')
    } else {
      openNotificationWithIcon('error', res.data.msg)
    }
  })
}
