  userName: [
    {required: true, message: '用户名不能为空', trigger: 'blur'},
    {min: 3, max: 12, message: '长度只能在 3 到 12 个字符', trigger: 'blur'},
  //     可由3-12位字母、数字、下划线或减号组成，不能以数字开头，不能包含中文
      {pattern: /^[a-zA-Z][a-zA-Z0-9_-]{2,12}$/, message: '用户名只能由3-12位字母、数字、下划线或减号组成，不能以数字开头，不能包含中文', trigger: 'blur'},
  ],
  password: [
    {required: true, message: '密码不能为空', trigger: 'blur'},
  ],
  email: [
    {required: true, message: '邮箱不能为空', trigger: 'blur'},
    {type: 'email', message: '请输入正确的邮箱地址', trigger: 'blur'},
  ],
  verificationCode: [
    {required: true, message: '验证码不能为空', trigger: 'blur'},
  ],
  rePassword: [
    {required: true, message: '重复密码不能为空', trigger: 'blur'},
    {
      validator: (rule, value, callback) => {
        if (value === registerForm.password) {
          callback();
        } else {
          callback(new Error('两次密码不一致'));
        }
      }, trigger: 'blur'
    },
  ],
});

const submitLoginForm = async (formEl) => {
  spinningLogin.value = true;
  if (!formEl) return;
  await formEl.validate(async (valid, fields) => {
    if (valid) {
      await submitLogin()
    } else {
      spinningLogin.value = false;
    }
  });
};
const submitRegisterForm = async (formEl) => {
  spinningRegister.value = true;
  if (!formEl) return;
  await formEl.validate(async (valid, fields) => {
    if (valid) {
      submitRegister()
    } else {
      spinningRegister.value = false;
    }
  });
};


async function submitLogin() {
  spinningLogin.value = true
  await requestService.post("/login", loginForm).then((res) => {
    if (res.data.code === 6001) {
      setLocalStorageItem("token", res.data.data.token, 5);
      setLocalStorageItem("uid", res.data.data.uid, 5);
      openNotificationWithIcon('success', res.data.msg)

      requestService.get('/user/').then((res) => {
        console.log(res)
        if (res.data.code === 2001) {
          console.log("获取用户信息成功")
          setLocalStorageItem("userInfo", res.data.data, 5)
          store.userInfo = res.data.data.userInfo
          store.followers = res.data.data.followers
          store.followings = res.data.data.followings
          store.isLogin = true
          onCloseLogin()
          requestService.get("/user/getRecommendUser").then((res) => {
            if (res.data.code === 2001) {
              store.recommendedUsers = res.data.data
            }
          })
          if (res.data.data.userInfo.newUser === 1 && getLocalStorageItem("tour") !== 1){
            console.log("新用户")
            setTimeout(() => {
              setLocalStorageItem("tour", 1, 5)
              handleOpen(true)
            },2500)
          }
        }
      })
    } else {
      openNotificationWithIcon('error', res.data.msg)
    }
  }).then(() => {
    spinningLogin.value = false
  })
}

const getCodeBtn = ref({
  text: '获取验证码',
  disabled: true
})
const getCode = () => {
  requestService.post("/mail/sendVerificationCodeHtmlMail", {
    to: registerForm.email,
    subject: "注册验证码",
    title: "邮箱验证",
    msg: "注册"
  }).then((res) => {
    if (res.data.code === 1001) {
      openNotificationWithIcon('success', res.data.msg)
      getCodeBtn.value.disabled = true
      getCodeBtn.value.text = ''
      timer.value = Date.now() + 1000 * 60
    } else {
      openNotificationWithIcon('warning', res.data.msg)
    }
  })
};


const emailBlur = () => {
  getAvatar()
  if (registerForm.email !== '') {
    getCodeBtn.value.disabled = false
  }
}

const getAvatar = () => {
  let email = registerForm.email;
  let userName = email.split("@")[0];
  let suffix = email.split("@")[1];

  if (suffix === "qq.com") {
    upload.value = false
    registerForm.avatar = "http://q1.qlogo.cn/g?b=qq&nk=" + userName + "&s=100";
    tempAvatar.value = registerForm.avatar
  }
};

const submitRegister = () => {
  spinningRegister.value = true
  if (upload.value === true && tempAvatar.value !== '') {
    upload.value = false
    uploadAvatar()
  } else {
    registerUser()
  }


}

const registerUser = () => {
  requestService.post("/user/register", {
    user: registerForm,
    verificationCode: registerForm.verificationCode
  }).then((res) => {
    if (res.data.code === 3001) {
      openNotificationWithIcon('success', res.data.msg)
      spinningRegister.value = false
      onCloseRegister()
    } else {
      openNotificationWithIcon('error', res.data.msg)
      spinningRegister.value = false
    }
  })
}


// 上传头像
const handleAvatarSuccess = async function (res, upload) {
  if (res.code === 1001) {
    ElMessage({
      type: 'success',
      message: '上传成功'
    })
    registerForm.avatar = res.data.filePath
    registerUser()
  } else {
    ElMessage({
      type: 'warning',
      message: res.msg
    })
  }
};

const token = ref("")

const beforeAvatarUpload = function (rawFile) {
  token.value = getLocalStorageItem("token")
  if (rawFile.type !== 'image/jpeg' && rawFile.type !== 'image/png' && rawFile.type !== 'image/gif') {
    ElMessage.error('头像只允许JPG、PNG或GIF格式!');
    return false;
  } else if (rawFile.size / 1024 / 1024 > 10) {
    ElMessage.error('上传的头像文件大小不能 10MB!');
    return false;
  }
  return true;
};

const uploadRef = ref();

function uploadAvatar() {
  return new Promise((resolve, reject) => {
    uploadRef.value.submit();

    resolve()
  })
}

const imgChange = (file, fileList) => {
  tempAvatar.value = URL.createObjectURL(file.raw)
}

const onClick = ({key}) => {
  if (key === '1') {
    store.active = 'editNormalMsg'
    router.push("/editUserMsg")
  }
  if (key === '2') {
    localStorage.removeItem("userInfo")
    localStorage.removeItem("token");
    localStorage.removeItem("uid");
    localStorage.removeItem("tour");
    store.userInfo = {};
    store.isLogin = false;
    store.active = "recommended";
    router.push("/recommended");
  }
}

</script>

