// 定义一个函数，用于获取localStorage中的数据，接收一个参数key，返回localStorage中key对应的值，首先判断localStorage中是否有key对应的值，如果有则返回，没有则返回空，
// 若有值则将其转换为JSON对象，判断其是否有expirationTime属性，如果有则判断其是否过期，如果过期则返回空，否则返回value，如果没有expirationTime属性则直接返回value。

// 获取localStorage
export const getLocalStorageItem = key => {
    if (!key) return;
    if (window.localStorage.getItem(key)) {
        let data = window.localStorage.getItem(key);
        let dataObj = JSON.parse(data);
        if (dataObj.expirationTime && new Date().getTime() > dataObj.expirationTime) {
            window.localStorage.removeItem(key);
        } else {
            return dataObj.value;
        }
    }
};

// 设置localStorage
// 定义一个函数，用于设置localStorage，接收三个参数key，value，expirationTime（单位：小时），首先判断key和value是否存在，如果不存在则返回空，
// 然后判断expirationTime是否存在，如果存在则将其转换为毫秒，然后将value和expirationTime存储到localStorage中，如果expirationTime不存在则只存储value。

export const setLocalStorageItem = (key, value, expirationTime) => {
    if (!key || !value) return;
    let expirationTimeMs = expirationTime ? new Date().getTime() + 1000 * 60 * 60 * expirationTime : null;
    if (expirationTimeMs) {
        let data = {
            value: value,
            expirationTime: expirationTimeMs
        };
        window.localStorage.setItem(key, JSON.stringify(data));

    } else {
        window.localStorage.setItem(key, value);
    }
}


export default {
    getLocalStorageItem,
    setLocalStorageItem
}

