import {createRouter, createWebHashHistory, createWebHistory} from 'vue-router'
import { message } from 'ant-design-vue';
import {store} from "@/store";
import {getLocalStorageItem} from "@/utils/data";

const routes = [
    {
        path: '/chat',
        name: 'chatHome',
        component: () => import("@/views/chatModel/index.vue"),
        redirect: '/chat/list',
        children: [
            {
                path: '/chat/list',
                name: "list",
                component: () => import("@/views/chatModel/user/userList.vue"),
                children: [
                    {
                        path: '/chat/chatWith',
                        name: "chatWith",
                        component: () => import("@/views/chatModel/chat/index.vue"),
                    },
                ]
            },
            {
                path: '/chat/add',
                name: "add",
                component: () => import("@/views/chatModel/user/userAdd.vue"),
                children: [
                    {
                        path: '/chat/userAdd',
                        name: "userAdd",
                        component: () => import("@/views/chatModel/user/add.vue"),
                    },
                ]
            },
        ]
    },
    {
        path:'/',
        name:'home',
        props: true,
        component: () => import("@/views/socialFeedModel/home/index.vue"),
        children: [
            {
                path: '/recommended',
                name: "recommended",
                props: true,
                component: () => import("@/views/socialFeedModel/home/pages/recommended.vue"),
            },
            {
                path: '/follow',
                name: "follow",
                component: () => import("@/views/socialFeedModel/home/pages/follow.vue"),
            },
            {
                path:'/user',
                name:"user",
                component: () => import("@/views/socialFeedModel/home/pages/user.vue"),
                children: [
                    {
                        path:'/userCenter/userCenter',
                        name: "userCenter",
                        component: () => import("@/views/socialFeedModel/home/pages/userCenter/userCenter.vue")
                    },
                    {
                        path:'/userCenter/followings',
                        name: "followings",
                        component: () => import("@/views/socialFeedModel/home/pages/userCenter/followings.vue")
                    },
                    {
                        path:'/userCenter/friends',
                        name: "friends",
                        component: () => import("@/views/socialFeedModel/home/pages/userCenter/friends.vue")
                    },
                    {
                        path:'/userCenter/otherUserCenter',
                        name: "otherUserCenter",
                        component: () => import("@/views/socialFeedModel/home/pages/userCenter/otherUserCenter.vue"),
                        meta: {

                        }
                    },
                ]
            },
            {
                path: '/postDetail',
                name: "postDetail",
                component: () => import("@/views/socialFeedModel/home/pages/postDetail.vue"),
            },
            {
                path: '/editUserMsg',
                name:"editUserMsg",
                component:()=>import("@/views/socialFeedModel/home/pages/editUserMsg.vue"),
                children: [
                    {
                        path: '/editUserMsg/editNormalMsg',
                        name:"editNormalMsg",
                        component:()=>import("@/views/socialFeedModel/home/pages/editUserMsg/editNormalMsg.vue"),
                    },
                    {
                        path: '/editUserMsg/editPasswd',
                        name:"editPasswd",
                        component:()=>import("@/views/socialFeedModel/home/pages/editUserMsg/editPasswd.vue"),
                    },
                ]
            },
        ]
    },



    {
        path:'/upload',
        name:'upload',
        component: () => import("@/views/upload/index.vue")
    },
    {
        path:'/test',
        name:'test',
        component: () => import("@/views/Temp/test.vue")
    },
]

const router = createRouter({
  // history: createWebHistory(process.env.BASE_URL),
    // hash模式
    history: createWebHashHistory(),
  routes
})

router.beforeEach((to, from, next) => {
    let token = getLocalStorageItem("token");  // 获取token
    // 如果请求的assets目录中的文件，直接放行
    if(to.path.startsWith("/assets")){
        return next();
    }
    if(to.name !== 'home' && to.name !== 'recommended'){  //如果不是去登录页面需要判断是否有token
        if (!token){
            message.error('请先登录');
            store.active = 'recommended';
            router.push({name:'recommended'});
        }else {
            next();
        }
    }else {
        next();
    }

})

export default router
